<template>
  <div>
    <mt-header fixed :title="getTitle">
      <mt-button v-if="active !== 0" icon="back" slot="left" @click="active = 0" >返回</mt-button>
      <mt-button icon="more" slot="right" @click="sheetVisible = true"></mt-button>
    </mt-header>
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>
    <section class="chatlist" v-if="active === 0">
      <ul>
        <template v-for="item in computedRecords">
          <li class="chat-mine" v-if="item.type === 1">
            <div class="chat-user">
              <img v-if="userInfo.webPortrait" :src="userInfo.webPortrait" />
              <img v-else src="../assets/user.png" /></div>
            <div class="time">
              <cite></cite>
            </div>
            <div class="chat-text" v-html="item.content.message"></div>
          </li>
          <li v-if="item.type === 2" :key="item.content.id">
            <div class="chat-user">
              <img src="../assets/default.png" />
            </div>
            <div class="time">
              <cite></cite>
            </div>
            <div class="chat-text" v-html="item.content.message"></div>
          </li>
        </template>
      </ul>
    </section>
    <mt-tab-container v-model="active">
      <mt-tab-container-item :id="0">
        <section class="foot">
          <form submit="return false;">
            <mt-field id="txtContent" :placeholder="placeholder" class="con" :disableClear="disableClear"
                      :readonly="readonly" v-model="content" @keyup.enter.native="sendMsg"></mt-field>
            <mt-field style="display: none"></mt-field>
            <span class="btn btn-send" v-on:click="sendMsg">发送</span>
          </form>
        </section>
      </mt-tab-container-item>
      <mt-tab-container-item :id="1">
        <div style="margin-top: 40px">
          <mt-field label="原密码" placeholder="请输入原密码" type="password" v-model="pass.oldPass" disableClear></mt-field>
          <mt-field label="新密码" placeholder="请输入新密码" type="password" v-model="pass.newPass" disableClear></mt-field>
          <mt-field label="确认新密码" placeholder="请确认新密码" type="password" v-model="pass.verifyPass" disableClear></mt-field>
          <Button type="primary" long style="margin-top: 20px;" @click="submitChangePass">修改密码</Button>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item :id="2">
        <div style="margin-top: 40px">
          <mt-field label="邮箱" placeholder="请输入邮箱" type="email" v-model="email.email" disableClear></mt-field>
          <mt-field placeholder="请输入验证码" v-model="email.code" disableClear>
            <mt-button type="default" @click="getBindingEmailCode">获取验证码</mt-button>
          </mt-field>
          <Button type="primary" long style="margin-top: 20px;" @click="submitBindingEmail">绑定邮箱</Button>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script>
/* eslint-disable */
import {userInfo, logout, changePass, bindingEmail, getEmailCode} from "../services/user"
import {saveMessage, ackMessage, getMessage} from "../services/message"
import {checkAuthorization, removeAuthorization, getAuthorization} from "../utils/request";
import { Toast } from 'mint-ui';

let socket;
export default {
  name: "chatlist",
  data() {
    return {
      active: 0,
      userInfo: undefined,
      content: "",
      topStatus: "",
      //聊天记录
      records: [],
      disableClear: true,
      placeholder: "请输入消息",
      readonly: false,
      limitSecond: 0,
      sheetVisible: false,
      actions: [
        {
          name: '退出登录',
          method: this.relogin
        },
        {
          name: '清空聊天记录',
          method: this.clearRecords
        },
        {
          name: '修改密码',
          method: this.toChangePass
        },
        {
          name: '绑定邮箱',
          method: this.toBindingEmail
        }
      ],
      pass: {
        newPass: '',
        oldPass: '',
        verifyPass: ''
      },
      email: {
        email: '',
        code: ''
      }
    };
  },
  props: {
    init: Object
  },
  methods: {
    selfMessage(message) {
      this.pushMessage(1, message);
      this.content = "";
    },
    quantumMessage(message) {
      this.pushMessage(2, message);
    },
    pushMessage(type, message) {
      if (type === 2) {
        if (this.records.some(item => item.content.id === message.id
          && item.content.timestamp === message.timestamp)) return;
      }
      this.records.push({
        type: type,
        content: message,
      });
      this.saveRecords();
      this.scrollToBottom();
    },
    relogin() {
      this.disconnectWebSocket()
      logout()
      removeAuthorization()
      window.location.reload();
    },
    toChangePass() {
      this.active = 1
    },
    toBindingEmail() {
      this.active = 2
    },
    clearRecords() {
      localStorage.removeItem("records" + this.userInfo.uid);
      this.records = [];
      this.quantumMessage({message: "当前聊天记录已清空！", type: 'text', timestamp: Math.floor(Date.now() / 1000)});
    },
    //发送消息
    sendMsg: function () {
      if (this.content === "") {
        Toast("请输入消息");
        return;
      }
      var msg = this.content;
      this.selfMessage({message: msg, type: 'text', timestamp: Math.floor(Date.now() / 1000)});
      this.placeholder = (this.init.webMessageGet || 5) + "秒后方可再次发起指令";
      this.readonly = true;

      this.limitSecond = this.init.webMessageGet || 5;
      this.changeLimitInfo();
      if (!this.userInfo) {
        window.location.reload();
      }
      /*saveMessage(msg)
        .then((res) => {
        if (res.data.code === 401) {
          this.$emit('updateValue', true);
        }
      })*/
      this.sendMessage(msg)
      return false;
    },
    connectWebSocket() {
      // 获取当前页面的协议（http 或 https）
      const protocol = window.location.protocol === 'https:' ? 'wss' : 'ws';
      // 获取当前页面的主机名和端口号
      const host = window.location.host;
      // 构建 WebSocket 地址
      const wsPath = '/ws'; // 相对路径
      const wsUrl = `${protocol}://${host}${wsPath}`;
      const token = getAuthorization();
      let t = this
      // 如果存在先关闭之前的连接
      if (socket) {
        socket.close();
      }

      // 连接WebSocket
      socket = new WebSocket(wsUrl, [token]);

      // 设置WebSocket事件处理程序
      socket.onopen = function() {
        console.log('WebSocket连接已打开');
      };

      socket.onmessage = function(event) {
        let data = JSON.parse(event.data)
        if (data.type === "REPLY") {
          console.log('收到消息:', data);
          data = data.data
          if (data && data.length > 0) {
            for (let i = 0; i < data.length; i++) {
              data[i].message = data[i].message
                .replace(/\r/g, "<br>")
                .replace(/\n/g, "<br>");
              t.quantumMessage(data[i]);
            }
          }
        } else if (data.type === "LOGOUT") {
          t.relogin()
        }
      }

      socket.onclose = function() {
        console.log('WebSocket连接已关闭');
      };

      socket.onerror = function(event) {
        console.error('WebSocket发生错误', event);
      };
    },
    sendMessage(message) {
      if (socket && socket.readyState === WebSocket.OPEN) {
        socket.send(JSON.stringify({
          data: message,
          type: "SEND"
        }));
        console.log('消息已发送:', message);
      } else {
        console.error('WebSocket连接未打开');
      }
    },
    disconnectWebSocket() {
      if (socket) {
        socket.close();
      }
    },
    saveRecords() {
      localStorage.setItem("records" + this.userInfo.uid, JSON.stringify(this.records));
    },
    //聚焦输入框
    focusTxtContent: function () {
      document.querySelector("#txtContent input").focus();
    },
    //滚动条滚动到底部
    scrollToBottom: function () {
      setTimeout(function () {
        let chatlist = document.getElementsByClassName("chatlist")[0];
        chatlist.scrollTop = chatlist.scrollHeight;
      }, 100);
    },
    handleTopChange(status) {
      this.topStatus = status;
    },
    changeLimitInfo() {
      var that = this;
      setTimeout(function () {
        that.limitSecond -= 1;
        that.placeholder = that.limitSecond + "秒后方可再次发起指令";
        if (that.limitSecond > 0) {
          that.changeLimitInfo();
        } else {
          that.placeholder = "请输入消息";
          that.readonly = false;
        }
      }, 1000);
    },
    getUserInfo() {
      if (checkAuthorization()) {
        userInfo().then(res => {
          if (res.data.code === 200) {
            this.userInfo = res.data.data
            this.records = JSON.parse(localStorage.getItem("records" + this.userInfo.uid)) || [];
            this.scrollToBottom();
          } else if (res.data.code === 401) {
            removeAuthorization()
            this.$Message.error("登录状态已失效，请重新登录")
            this.$emit('updateValue', true);
          }
        })
      }
    },
    submitChangePass() {
      let {newPass,oldPass,verifyPass} = this.pass;
      if (!(newPass || oldPass || verifyPass)) {
        Toast('请补充表单');
        return;
      }
      if (newPass !== verifyPass) {
        Toast('两次输入密码不一致');
        return;
      }
      changePass(this.pass).then((res) => {
        Toast(res.data.message);
      })
    },
    getBindingEmailCode() {
      if (!this.email.email) {
        Toast('请输入邮箱');
        return;
      }
      getEmailCode(this.email.email).then((res) => {
        Toast(res.data.message);
      })
    },
    submitBindingEmail() {
      let {email,code} = this.email;
      if (!(email || code)) {
        Toast('请补充表单');
        return;
      }
      bindingEmail(this.email).then((res) => {
        Toast(res.data.message);
      })
    }
  },
  computed: {
    computedRecords() {
      return this.records.sort((a, b) => a.content.timestamp - b.content.timestamp)
    },
    getTitle() {
      switch (this.active) {
        case 0:
          return this.userInfo ? this.userInfo.webUsername : 'watchdog';
        case 1:
          return "修改密码"
        case 2:
          return "绑定邮箱"
      }
    }
  },
  mounted: function () {
    this.getUserInfo();
    this.connectWebSocket();
    this.focusTxtContent();
  },
};
</script>

<style>
.chatlist {
  position: absolute;
  bottom: 80px;
  left: 0px;
  right: 0px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 10px;
  top: 40px;
}

.chatlist-bottom {
  bottom: 80px;
}

.chatlist-bottom-collapse {
  bottom: 198px;
}

.chatlist ul {
  min-height: 300px;
}

.chatlist ul .chat-mine {
  text-align: right;
  padding-left: 0;
  padding-right: 60px;
}

.chatlist ul li {
  position: relative;
  /*font-size: 0;*/
  margin-bottom: 10px;
  padding-left: 60px;
  min-height: 58px;
}

.chat-mine .chat-user {
  left: auto;
  right: 3px;
}

.qk-span {
  height: 32px;
  line-height: 32px;
  bottom: 48px;
  background-color: #fff;
}

.qk-span span {
  margin-left: 4px;
  border: 1px solid #eeeeee;
  padding: 4px 8px;
  background-color: #33df83;
  color: #fff;
  size: 9px;
  border-radius: 12px;
}

.chat-user {
  position: absolute;
  left: 3px;
}

.chat-text,
.chat-user {
  display: inline-block;
  vertical-align: top;
  /*font-size: 14px;*/
}

.chat-user img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
}

.time {
  width: 100%;
}

cite {
  left: auto;
  right: 60px;
  text-align: right;
}

cite {
  font-style: normal;
  line-height: 24px;
  font-size: 12px;
  white-space: nowrap;
  color: #999;
  text-align: left;
}

cite i {
  font-style: normal;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 12px;
}

.chat-mine .chat-text {
  margin-left: 0;
  text-align: left;
  background-color: #33df83;
  color: #fff;
}

.chat-text {
  position: relative;
  line-height: 22px;
  /*margin-top: 25px;*/
  padding: 10px;
  background-color: #eee;
  border-radius: 3px;
  color: #333;
  word-break: break-all;
  max-width: 462px\9;
}

.chat-text,
.chat-user {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
}

.chat-text img {
  max-width: 100%;
  vertical-align: middle;
}

.chat-user {
  position: absolute;
  left: 3px;
}

.chat-text:after {
  content: "";
  position: absolute;
  left: -10px;
  top: 15px;
  width: 0;
  height: 0;
  border-style: solid dashed dashed;
  border-color: #eee transparent transparent;
  overflow: hidden;
  border-width: 10px;
}

