/* eslint-disable */
import {MESSAGE} from './api'
import {METHOD, request} from '../utils/request'


/**
 *  确认消息被成功获取
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function ackMessage(params) {
  return request(MESSAGE.ACK + '?ackId=' + params, METHOD.PUT)
}


/**
 *  获取自己的回复信息
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function getMessage() {
  return request(MESSAGE.BASE, METHOD.GET)
}


/**
 *  用户端发送消息指令
 * @param params {Object}
 * @returns {Promise<AxiosResponse<T>>}
 */
export async function saveMessage(params) {
  return request(MESSAGE.BASE + '?message=' + params, METHOD.POST)
}






