<template>
  <div id="app">
    <ChatList v-if="!loginModal.show" :init="loginModal.init" @updateValue="updateValue"></ChatList>
    <Modal :footer-hide="true" :mask-closable="false" :closable="false" v-model="loginModal.show" :fullscreen="true"
           style="overflow:hidden">
      <div class="login" style="overflow:hidden">
        <div class="card" :padding="30">
          <img style="width:auto;height:70px;display:block;margin:0 auto" alt="logo" src="./assets/default.png">
          <div v-if="loginModal.model === 0" style="font-size: 14px;color: #808695;text-align:center;padding:20px">用户登录
          </div>
          <div v-else-if="loginModal.model === 1" style="font-size: 14px;color: #808695;text-align:center;padding:20px">
            用户注册
          </div>
          <div v-else-if="loginModal.model === 2" style="font-size: 14px;color: #808695;text-align:center;padding:20px">
            重置密码
          </div>
          <Form ref="loginForm" :model="loginModal.formItem" :rules="loginModal.ruleValidate" :label-width="0">
            <FormItem prop="webUsername" :key="1">
              <Input size="large" placeholder="请输入用户名" v-model="loginModal.formItem.webUsername">
                <Icon type="ios-contact" slot="prefix"/>
              </Input>
            </FormItem>
            <FormItem v-if="loginModal.model === 0 || loginModal.model === 1" prop="webPassword" :key="2">
              <Input size="large" placeholder="请输入密码" type="password" password
                     v-model="loginModal.formItem.webPassword">
                <Icon type="ios-lock" slot="prefix"/>
              </Input>
            </FormItem>
            <FormItem v-if="loginModal.model === 1 && this.loginModal.init.isEnableMail" prop="email" :key="3">
              <Input size="large" placeholder="请输入邮箱" v-model="loginModal.formItem.email">
                <Icon type="ios-mail" slot="prefix"/>
              </Input>
            </FormItem>
            <FormItem v-if="loginModal.model === 2" prop="reEmail" :key="3">
              <Input size="large" placeholder="请输入绑定的邮箱" v-model="loginModal.formItem.reEmail">
                <Icon type="ios-mail" slot="prefix"/>
              </Input>
            </FormItem>
            <FormItem v-if="loginModal.model === 2" prop="code" :key="4">
              <Input size="large" placeholder="请输入邮箱验证码" v-model="loginModal.formItem.code">
                <Icon type="ios-mail-open" slot="prefix"/>
                <Button slot="append" @click="codeMode = true">获取验证码</Button>
              </Input>
            </FormItem>
            <FormItem v-if="loginModal.model === 2" prop="rePassword" :key="5">
              <Input size="large" placeholder="请输入密码" type="password" password
                     v-model="loginModal.formItem.rePassword">
                <Icon type="ios-unlock" slot="prefix"/>
              </Input>
            </FormItem>
            <FormItem v-if="loginModal.model === 2" prop="reOldPassword" :key="6">
              <Input size="large" placeholder="请再次输入密码" type="password" password
                     v-model="loginModal.formItem.reOldPassword">
                <Icon type="ios-lock" slot="prefix"/>
              </Input>
            </FormItem>
          </Form>
          <div class="flexBetween" v-if="loginModal.init.isEnableRegister || loginModal.init.isEnableMail">
            <p v-if="showRegisterLink" size="large" @click="register" type="text">没账号？注册一个？</p>
            <p v-if="showLoginLink" size="large" @click="loginModal.model = 0" type="text">有账号，去登录</p>
            <p v-if="showForgetPasswordLink" size="large" @click="loginModal.model = 2" type="text">忘记密码？</p>
          </div>
          <Button v-if="loginModal.model === 0" type="primary" size="large" @click="handleSubmit('loginForm')"
                  class="loginButton">登录
          </Button>
          <Button v-else-if="loginModal.model === 1" type="primary" size="large" @click="submitRegister('loginForm')"
                  class="loginButton">注册
          </Button>
          <Button v-else-if="loginModal.model === 2" type="primary" size="large" @click="submitResetPass('loginForm')"
                  class="loginButton">重置
          </Button>
        </div>
      </div>
    </Modal>
    <Modal v-model="codeMode" :mask-closable="false" width="360">
      <template #header>
        <p style="text-align:center">
          <Icon type="ios-information-circle"></Icon>
          <span>图形验证码</span>
        </p>
      </template>
      <div style="text-align:center">
        <Row justify="center" align="middle" :gutter="24">
          <Col span="15">
            <Input size="large" placeholder="请输入邮箱验证码" v-model="loginModal.formItem.reCode">
              <Icon type="ios-ionic" slot="prefix"/>
            </Input>
          </Col>
          <Col span="9">
            <img :src="codeUrl" @click="updateCaptcha">
          </Col>
        </Row>
      </div>
      <template #footer>
        <Button type="success" size="large" long @click="getEmailCode">确认</Button>
      </template>
    </Modal>
  </div>
</template>

<script type="module">
/* eslint-disable */
import {getInit} from "./services/webApp"
import {register, login, resetPass, resetCode} from "./services/user"
import ChatList from "./components/ChatList";
import {setAuthorization, checkAuthorization} from "./utils/request";
// import Input from 'iview/src/components/input';
const BASE_URL = ""
export default {
  name: "app",
  components: {
    ChatList
  },
  data() {
    return {
      codeUrl: BASE_URL + '/web/graphCode?time=' + new Date().getTime(),
      codeMode: false,
      loginModal: {
        show: false,
        model: 0, // 0: 登录，1：注册 2：忘记密码
        init: {},
        formItem: {
          webUsername: "",
          webPassword: "",
          email: "",
          reEmail: "",
          code: "",
          reCode: "",
          rePassword: "",
          reOldPassword: ""
        },
        // 校验规则
        ruleValidate: {
          webUsername: [{
            required: true,
            message: "输入用户名",
            trigger: "blur",
          },],
          webPassword: [{
            required: true,
            message: "输入密码",
            trigger: "blur",
          },],
          email: [{
            required: true,
            message: "输入绑定邮箱",
            trigger: "blur",
          },],
          reEmail: [{
            required: true,
            message: "输入绑定的邮箱",
            trigger: "blur",
          },],
          code: [{
            required: true,
            message: "输入接收的邮箱验证码",
            trigger: "blur",
          },],
          rePassword: [{
            required: true,
            message: "输入重置密码",
            trigger: "blur",
          },],
          reOldPassword: [{
            required: true,
            message: "再次确认重置密码",
            trigger: "blur",
          },],
        },
      }
    }
  },
  methods: {
    updateValue(newValue) {
      this.loginModal.show = newValue;
    },
    register() {
      this.loginModal.model = 1;
    },
    // 提交表单
    handleSubmit(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          login({
            webUsername: this.loginModal.formItem.webUsername,
            webPassword: this.loginModal.formItem.webPassword
          }).then((res) => {
            let messageType = res.data.code !== 200 ? "error" : "success";
            this.$Message[messageType]({
              background: true,
              content: res.data.message
            });
            if ("success" === messageType) {
              setAuthorization(res.data.data)
              location.reload();
            }
          }).catch((err) => {
            console.log(err);
          });
        }
      });
    },
    submitRegister(name) {
      // 校验表单数据
      this.$refs[name].validate((valid) => {
        if (valid) {
          register({
            webUsername: this.loginModal.formItem.webUsername,
            webPassword: this.loginModal.formItem.webPassword,
            webEmail: this.loginModal.formItem.email
          }).then((res) => {
            let messageType = res.data.code !== 200 && res.data.code !== 410
              ? "error" : "success";
            this.$Message[messageType]({
              background: true,
              content: res.data.message
            });
          }).catch((err) => {
            console.log(err);
          });
        }
      });
    },
    submitResetPass(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          resetPass({
            username: this.loginModal.formItem.webUsername,
            email: this.loginModal.formItem.reEmail,
            code: this.loginModal.formItem.code,
            newPass: this.loginModal.formItem.rePassword,
            verifyPass: this.loginModal.formItem.reOldPassword,
          }).then((res) => {
            let messageType = "error"
            let message = res.data.message
            if (res.data.code === 200) {
              messageType = "success";
              message = res.data.data
            }
            this.$Message[messageType]({
              background: true,
              content: message
            });
          }).catch((err) => {
            console.log(err);
          });
        }
      });
    },
    // 获取邮箱验证码
    getEmailCode() {
      resetCode({
        username: this.loginModal.formItem.webUsername,
        email: this.loginModal.formItem.reEmail,
        code: this.loginModal.formItem.reCode,
      }).then((res) => {
        let messageType = "error"
        let message = res.data.message
        if (res.data.code === 200) {
          messageType = "success";
          message = res.data.data
          this.codeMode = false;
        } else {
          this.updateCaptcha()
        }
        this.$Message[messageType]({
          background: true,
          content: message
        });
      }).catch((err) => {
        console.log(err);
      });
    },
    updateCaptcha() {
      this.codeUrl = BASE_URL + '/web/graphCode?time=' + new Date().getTime();
    },
    checkLogin() {
      if (!checkAuthorization()) {
        this.loginModal.show = true;
      }
    }
  },
  mounted() {
    this.checkLogin();
    getInit().then(res => {
      this.loginModal.init = res.data.data;
    })
  },
  computed: {
    showRegisterLink() {
      return (this.loginModal.model === 0 || this.loginModal.model === 2) && this.loginModal.init.isEnableRegister;
    },
    showLoginLink() {
      return (this.loginModal.model === 1 || this.loginModal.model === 2);
    },
    showForgetPasswordLink() {
      return (this.loginModal.model === 0 || this.loginModal.model === 1) && this.loginModal.init.isEnableMail;
    }
  }
};
</script>

<style>
@import "../node_modules/iview/dist/styles/iview.css";
@import "../node_modules/font-awesome/css/font-awesome.min.css";


.ivu-modal-fullscreen .ivu-modal-content .ivu-modal-body {
  overflow: hidden !important;
}

body {
  margin: 0px;
  padding: 0px;
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB,
  Microsoft YaHei, SimSun, sans-serif;
  overflow: hidden;
}

blockquote,
body,
button,
dd,
div,
dl,
dt,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
li,
ol,
p,
pre,
td,
textarea,
th,
ul {
  margin: 0;
  margin-top: 0px;
  margin-right: 0px;
  margin-bottom: 0px;
  margin-left: 0px;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

ul,
li {
  list-style: none;
}

a {
  color: #666;
}


::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #f8f8f8;
}


::-webkit-scrollbar-track {
  background-color: #f8f8f8;
}

::-webkit-scrollbar-thumb {
  background-color: #e2e2e2;
}

#app {
  overflow: hidden;
}

.flexBetween {
  height: 6vh;
  line-height: 6vh;
}

.flexBetween p {
  float: left;
}

.flexBetween p:last-child {
  text-align: right;
  float: right;
}
</style>



