import Vue from 'vue'
// import MintUI from 'mint-ui'
import {Toast,Header,Actionsheet,TabContainer,TabContainerItem,Field, Button} from 'mint-ui'
import 'mint-ui/lib/style.css'
import App from './App'
import axios from 'axios'
import iView from "iview";
// import iview  from "./iviews"
import 'iview/dist/styles/iview.css'
import {loadInterceptors} from './utils/request'
import interceptors from './utils/axios-interceptors'


Vue.prototype.$http= axios
// Vue.use(MintUI)
Vue.component(Toast.name, Toast);
Vue.component(Header.name, Header);
Vue.component(Actionsheet.name, Actionsheet);
Vue.component(TabContainer.name, TabContainer);
Vue.component(TabContainerItem.name, TabContainerItem);
Vue.component(Field.name, Field);
Vue.component(Button.name, Button);

// Vue.component('Input', inputComp);
// Vue.use(iview)
Vue.use(iView, {
})
console.log(Vue.prototype.$Message)
loadInterceptors(interceptors, {message: Vue.prototype.$Message})
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App }
})



